.project {
	max-width: 370px;
	background-color: var(--project-card-bg);
	box-shadow: var(--box-shadow);
	border-radius: 10px;
	transition: transform 250ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.project:hover {
	cursor: pointer;
	transform: scale(1.1);
}

.project__img {
	border-radius: 10px;
}

.project__title {
	padding: 15px 20px 25px;
	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
	color: var(--project-card-text);
	text-align: center;
}